.questions {
  margin: 0 2em 5em;
}

.card {
  margin: 1em auto;
  padding: 0.5em 0 1em;
  max-width: 400px;
  text-align: center;
  border-radius: 0.25em;
  -webkit-box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.75);
}

.card button {
  margin: 1em auto 0.5em;
}

.testList {
  margin: 0 2em;
}
